<template>
    <div class="circle-pie-chart-container">
        <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
        <div :id="circlePieChartId" :style="{ width, height }"></div>
    </div>
</template>

<script>
import * as echarts from "echarts";
export default {
    props: {
        circlePieChartId: {
            type: String,
            default: "main",
        },
        width: {
            type: String,
            default: "100%",
        },
        height: {
            type: String,
            default: "600px",
        },
        echartsData: {
            type: Object,
            default: () => ({
                name: "",
                value: 0,
            }),
        },
        total: {
            type: Number,
            default: 100,
        },
        // title
        echartTitle: {
            type: Object,
            default: () => ({
                text: "",
            }),
        },
        colorArr: {
            type: Array,
            default: () => ["#fde4be", "#ec963d"],
        },
    },
    data() {
        return {
            myChart: null,
            pass_rate: 70,
            total: 100,
            myOptions: {
                title: {
                    text: "",
                    left: "center",
                    top: "center",
                    textStyle: {
                        //主标题文本样式{"fontSize": 18,"fontWeight": "bolder","color": "#333"}
                        // fontFamily: "Arial, Verdana, sans...",
                        fontSize: "25",
                        fontStyle: "normal",
                        fontWeight: "bold",
                        color: "#fff",
                    },
                },
                // legend: {
                //     top: "5%",
                //     left: "center",
                // },
                tooltip: {
                    trigger: "item",
                },
                series: [
                    {
                        // name: "法法师 ",
                        type: "pie",
                        radius: ["40%", "70%"],
                        avoidLabelOverlap: false,
                        label: {
                            show: false,
                            position: "center",
                        },
                        // emphasis: {
                        //     label: {
                        //         show: true,
                        //         fontSize: "40",
                        //         fontWeight: "bold",
                        //     },
                        // },

                        labelLine: {
                            show: false,
                        },
                        data: [],
                        // itemStyle: {
                        //     color: (params) => {
                        //         return this.colorArr[params.dataIndex];
                        //         // if(params.value>0 && params.value <=50){
                        //         //     return '#00e600'
                        //         // }else if(params.value>50 && params.value <=100){
                        //         //     return '#ffff00'
                        //         // }else if(params.value>100 && params.value <=150){
                        //         //     return '#ff7f00'
                        //         // }else if(params.value>150 && params.value <=200){
                        //         //     return '#ff0000'
                        //         // }else if(params.value>200 && params.value <=300){
                        //         //     return '#9a004a'
                        //         // }else if(params.value>300){
                        //         //     return '#800021'
                        //         // }
                        //     },
                        // },
                    },
                ],
            },
        };
    },
    watch: {
        echartsData() {
            // 初始化图表
            this.initCharts();
        },
    },
    mounted() {
        // 初始化图表
        this.initCharts();
    },
    methods: {
        // 初始化图表
        initCharts() {
            this.myChart = echarts.init(
                document.getElementById(this.circlePieChartId)
            );
            this.myChart.setOption(this.myOptions);
            this.myChart.setOption({
                series: [
                    {
                        // data: this.echartsData,
                        data: [
                            {
                                // name: this.echartsData.value + "%",
                                value: this.echartsData.value,
                                itemStyle: {
                                    normal: {
                                        color: {
                                            type: "linear",
                                            x: 0,
                                            y: 0,
                                            x2: 0,
                                            y2: 1,
                                            colorStops: [
                                                { offset: 0, color: this.colorArr[0] },
                                                { offset: 1, color: this.colorArr[1] },
                                            ],
                                            globalCoord: true, // 缺省为 false
                                        },
                                    },
                                },
                            },
                            {
                                value: this.total - this.echartsData.value,
                                itemStyle: {
                                    normal: {
                                        color: "#414466",
                                    },
                                },
                            },
                        ],
                    },
                ],
                title: {
                    text: this.echartTitle.text,
                },
            });
            window.addEventListener("resize", this.onResize);
        },
        onResize() {
            this.myChart.resize();
        },
    },
};
</script>

